body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#loading {
  width: 100px;
  height: 100vh;
  align-items: center;
  justify-content: center;
  margin: 40vh auto;
  position: relative;
  z-index: 99999;
}


.ring:before
{
  content: '';
  border-radius: 50%;
  border: 2px solid #333;
  height: 30px;
  width: 100px;
  position: absolute;
}

.ring1:before
{
  left: 40px;
  width: 20px;
  top: 12px;
  height: 6px;
  animation: 2.5s ease 0s infinite spinner;
}

.ring2:before
{
  left: 35px;
  width: 30px;
  top: 10.5px;
  height: 9px;
  animation: 2.5s ease 0.1s infinite spinner;
}

.ring3:before
{
  left: 30px;
  width: 40px;
  top: 9px;
  height: 12px;
  animation: 2.5s ease 0.2s infinite spinner;
}

.ring4:before
{
  left: 25px;
  width: 50px;
  top: 7.5px;
  height: 15px;
  animation: 2.5s ease 0.3s infinite spinner;
}

.ring5:before
{
  left: 20px;
  width: 60px;
  top: 6px;
  height: 18px;
  animation: 2.5s ease 0.4s infinite spinner;
}

.ring6:before
{
  left: 15px;
  width: 70px;
  top: 4.5px;
  height: 21px;
  animation: 2.5s ease 0.5s infinite spinner;
}

.ring7:before
{
  left: 10px;
  width: 80px;
  top: 3px;
  height: 24px;
  animation: 2.5s ease 0.6s infinite spinner;
}

.ring8:before
{
  left: 5px;
  width: 90px;
  top: 1.5px;
  height: 27px;
  animation: 2.5s ease 0.7s infinite spinner;
}

.ring9:before
{
  width: 100px;
  height: 30px;
  animation: 2.5s ease 0.8s infinite spinner;
}

@keyframes spinner {
  0% {
    transform: translateY(10px);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
  }
  50% {
    transform: translateY(60px);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
  }
  100% {
    transform: translateY(10px);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
  }
}
